.card {
    padding-top: 24px;
    padding-left: 28px;
    position: relative;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 10px 22px -10px lightblue;
}

.card h3 {
    font-size: 20px;
    margin: 0;
    color: var(--Very-Dark-Blue);
}

.card p {
    margin: 0;
    padding-top: 3px;
    font-size: 13.3px;
    line-height: 1.7;
    padding-right: 23px;
    color: var(--Grayish-Blue);
}

.card img {
    position: absolute;
    right: 29px;
    bottom: 29px;
    height: 57px;
    width: 57px;
}

.supervisor {
    margin-bottom: 8px;
    border-top: 3.2px solid var(--Cyan);
}

.team.builder {
    margin-bottom: 6px;
    border-top: 3.2px solid var(--Red);
}

.karma {
    margin-bottom: 7px;
    border-top: 3.2px solid var(--Orange);
}

.calculator {
    border-top: 3.2px solid var(--Blue);
}

@media (min-width: 800px) {
    .card {
        padding-top: 27px;
        padding-left: 32px;
    }
    .card p {
        padding-top: 8px;
        font-size: 13.2px;
        line-height: 1.75;
    }
    .card img {
        height: 64px;
        width: 64px;
        right: 33px;
        bottom: 33px;
    }

    .team.builder {
        grid-area: sec;
        margin-bottom: 13px;
        border-top: 5px solid var(--Red);
    }
    .karma {
        grid-area: thr;
        margin-bottom: 0;
        border-top: 5px solid var(--Orange);
    }

    .supervisor {
        grid-area: fir;
        align-self: center;
        height: 252px;
        margin-bottom: 0;
        border-top: 5px solid var(--Cyan);
    }
    .supervisor img {
        bottom: 33px;
    }
    .calculator {
        grid-area: fou;
        align-self: center;
        height: 252px;
        border-top: 5px solid var(--Blue);
    }
}