.App {
  display: grid;
  grid-template-rows: 226px 231px 229px 230px 222px;
  grid-gap: 1em;
  padding-top: 5em;
  padding-left: 2em;
  padding-right: 2em;
  padding-bottom: 77px;
  font-family: "Poppins", sans-serif;
  background-color: var(--Very-Light-Gray);
  /* opacity: 0.5; */
}

@media (min-width: 800px) {
  .App {
    min-height: 100vh;
    grid-template-areas: "main main main"
                         "fir sec fou"
                         "fir thr fou";
    grid-template-columns: 1fr 352px 1fr;
    grid-template-rows: 222px 265px 252px;
    padding-top: 73px;
    padding-left: 165px;
    padding-right: 165px;
    column-gap: 28px;
  }
}


@font-face {
  font-family: "Poppins";
  font-weight: 200;
  src: url("./fonts/Poppins-ExtraLight.ttf") format("truetype");
}

@font-face {
  font-family: "Poppins";
  font-weight: 400;
  src: url("./fonts/Poppins-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Poppins";
  font-weight: 600;
  src: url("./fonts/Poppins-SemiBold.ttf") format("truetype");
}
