main {
    padding-top: 4px;

}

main h1 {
    margin: 0;
    font-size: 23.5px;
    font-weight: 200;
    padding-left: 7px;
    color: var(--Very-Dark-Blue);
}

main h2 {
    margin: 0;
    padding-top: 1px;
    font-size: 24.4px;
    padding-left: 7px;
    color: var(--Very-Dark-Blue);
}

main p {
    text-align: center;
    font-size: 15.2px;
    line-height: 1.63;
    padding-right: 2px;
    color: var(--Grayish-Blue);
}

@media (min-width: 800px) {
    main {
        grid-area: main;
        text-align: center;
    }
    main h1 {
        font-size: 36.5px;
        padding-left: 0;
    }
    main h2 {
        font-size: 36.5px;
        padding-top: 0;
        padding-left: 0;
        line-height: 1.3;
    }
    main p {
        padding-top: 4px;
        padding-left: 280px;
        padding-right: 280px;
    }
}